import React, { Component } from "react";
import InputTextField from "../../common_components/textInput.jsx";
import Province from "./ad_ed_gd_components/province.jsx";
import Cities from "./ad_ed_gd_components/cities.jsx";
import Qualification from "./ad_ed_gd_components/qualification.jsx";
import QualificationCategories from "./ad_ed_gd_components/qualificationCategories.jsx";
import DoYouHaveLaptop from "./ad_ed_gd_components/haveLaptop.jsx";
import Gender from "../../common_components/gender.jsx";
import {
  handleChange,
  handleChangeQualification,
  handleChangeLaptop
} from "../../action/index";
import { connect } from "react-redux";
class AddressEducationGuardian extends Component {
  componentDidMount() {
    window.scroll(0, 0);
  }
  render() {
    console.log(this.props);
    return (
      <React.Fragment>
        <div className="form">
          <form action="#" className="formTag">
            <h1 className="formHeading">Student Address</h1>
            <Province
              asterisk="*"
              handleChange={e => this.props.handleChange(e)}
            />
            <Cities
              asterisk="*"
              handleChange={e => this.props.handleChange(e)}
              province={this.props.province}
            />
            <InputTextField
              titleClass="selectTitle"
              asterisk="*"
              title="Present Address"
              inputClass="selectInput form-control"
              subTitle=""
              subTitleClass="none"
              htmlFor="studentpresentaddress"
              type="text"
              name="student_present_address"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Student Present Address"
            />
            <InputTextField
              titleClass="selectTitle"
              asterisk="*"
              title="Permanant Address"
              inputClass="selectInput form-control"
              subTitle=""
              subTitleClass="none"
              htmlFor="student_permanant_address"
              type="text"
              name="student_permanant_address"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Student Permanant Address"
              pattern="[0-9]{11}"
            />
            <h1 className="formHeading mt-3">Education</h1>
            <Qualification
              asterisk="*"
              handleChangeQualification={e =>
                this.props.handleChangeQualification(e)
              }
            />
            <QualificationCategories
              asterisk="*"
              qualification={this.props.qualification}
              qualificationCategories={this.props.qualificationCategories}
              handleChange={e => this.props.handleChange(e)}
            />
            <InputTextField
              titleClass="selectTitle"
              title="Institute Name"
              asterisk="*"
              inputClass="selectInput form-control"
              subTitle=""
              subTitleClass="none"
              htmlFor="institute"
              type="text"
              name="institute"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Institute Name"
            />
            <DoYouHaveLaptop
              asterisk="*"
              handleChange={e => this.props.handleChangeLaptop(e)}
            />
            <h1 className="formHeading mt-1">Father / Guardian</h1>
            <InputTextField
              titleClass="selectTitle"
              asterisk="*"
              title="Father / Guardian Name"
              inputClass="selectInput form-control"
              subTitle=""
              subTitleClass="none"
              htmlFor="father_guardian_name"
              type="text"
              name="father_guardian_name"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Father / Guardian Name"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Father / Guardian Email"
              inputClass="selectInput form-control"
              subTitle=""
              asterisk="*"
              subTitleClass="none"
              htmlFor="father_guardian_email"
              type="email"
              name="father_guardian_email"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Father / Guardian Email"
              pattern="[0-9]{13}"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Father / Guardian Contact No."
              inputClass="selectInput form-control"
              subTitle=""
              asterisk="*"
              errorTitle="Enter Your 11 Digit Mobile Number."
              subTitleClass="none"
              htmlFor="father_guardian_number"
              type="text"
              name="father_guardian_number"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Father / Guardian Contact No."
              pattern="[0-9]{11}"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Father / Guardian CNIC"
              inputClass="selectInput form-control"
              subTitle=""
              asterisk="*"
              errorTitle="Enter Your 13 Digit CNIC Number."
              subTitleClass="none"
              htmlFor="father_guardian_CNIC"
              type="text"
              name="father_guardian_CNIC"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Father / Guardian CNIC"
              pattern="[0-9]{13}"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Father / Guardian Present Address"
              inputClass="selectInput form-control"
              subTitle=""
              subTitleClass="none"
              htmlFor="father_guardian_present_address"
              type="text"
              asterisk="*"
              name="father_guardian_present_address"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Father / Guardian Present Address"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Father / Guardian Permanant Address"
              inputClass="selectInput form-control"
              subTitle=""
              subTitleClass="none"
              htmlFor="father_guardian_permanant_address"
              type="text"
              asterisk="*"
              name="father_guardian_permanant_address"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Father / Guardian Permanant Address"
              pattern="[0-9]{11}"
            />
            <Gender
              asterisk="*"
              gender="fatherOrGuardianGender"
              handleChange={e => this.props.handleChange(e)}
            />

            {this.props.province !== "" &&
            this.props.province !== "Select" &&
            this.props.district !== "" &&
            this.props.district !== "Select" &&
            this.props.student_permanant_address !== "" &&
            this.props.student_present_address !== "" &&
            this.props.qualification !== "" &&
            // this.props.qualificationCategories === "" &&
            this.props.qualification !== "Select" &&
            // this.props.qualificationCategories === "Select" &&
            this.props.institute !== "" &&
            this.props.haveLaptop !== "" &&
            this.props.father_guardian_CNIC !== "" &&
            this.props.father_guardian_email !== "" &&
            this.props.father_guardian_name !== "" &&
            this.props.father_guardian_number !== "" &&
            this.props.father_guardian_permanant_address !== "" &&
            this.props.father_guardian_present_address !== "" &&
            this.props.fatherOrGuardianGender !== "" &&
            this.props.fatherOrGuardianGender !== "Select" ? (
              <button type="submit" className="submitBtn border-0 w-100">
                Submit
              </button>
            ) : (
              <button type="submit" className="nextBtnDisable border-0 w-100">
                Submit
              </button>
            )}
          </form>
        </div>
      </React.Fragment>
    );
  }
}

// state
const mapStateToProps = state => {
  return state;
};

// Actions
const mapDispatchToProps = {
  handleChange,
  handleChangeQualification,
  handleChangeLaptop
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AddressEducationGuardian);
