import React, { Component } from "react";
class CityOfStudy extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <p className="selectTitle">City Of Study {this.props.asterisk}</p>
          <select
            name="city"
            onChange={this.props.handleChange}
            className="selectInput form-control"
          >
            <option value="Select">Select</option>
            <option value="Lahore">Lahore</option>
            <option value="Islamabad">Islamabad</option>
            <option value="Peshawar">Peshawar</option>
          </select>
          <p className="selectPara">
            City of study means, in which city you will attend classes
          </p>
          <p className="selectPara">
            For online students City of study means, in which city you will
            appear for exams in our centers
          </p>
        </div>
      </React.Fragment>
    );
  }
}

export default CityOfStudy;
