import React, { Component } from "react";
class HaveYouAnyPriorExperience extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <p className="selectTitle">
            Have You Any Prior Experience in your selected program?{" "}
            {this.props.asterisk}
          </p>
          <select
            name="priorExperience"
            onChange={this.props.handleChange}
            className="selectInput form-control"
          >
            <option>Select</option>
            <option>Yes</option>
            <option>No</option>
          </select>
        </div>
      </React.Fragment>
    );
  }
}

export default HaveYouAnyPriorExperience;
