import React, { Component } from "react";
class CourseTimingPreference extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <p className="selectTitle">
            Class Timing Preference {this.props.asterisk}
          </p>
          <select
            name="timingPreference"
            onChange={this.props.handleChange}
            className="selectInput form-control"
          >
            <option>Select</option>
            <option>Morning</option>
            <option>Evening</option>
          </select>
        </div>
      </React.Fragment>
    );
  }
}

export default CourseTimingPreference;
