import React, { Component } from "react";
class CampusLocationPreference extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <p className="selectTitle">
            Campus Location Preference {this.props.asterisk}
          </p>
          <select
            name="locationPreference"
            onChange={this.props.handleChange}
            className="selectInput form-control"
          >
            {this.props.city === "" || this.props.city === "Select" ? (
              <option>Firstly Select City of Study</option>
            ) : null}
            {this.props.city === "Lahore" ? (
              <React.Fragment>
                <option>Select</option>
                <option>Gulburg</option>
                <option>Defence</option>
                <option>Johar Town</option>
              </React.Fragment>
            ) : null}
            {this.props.city === "Islamabad" ? (
              <React.Fragment>
                <option selected>Select</option>
              </React.Fragment>
            ) : null}
            {this.props.city === "Peshawar" ? (
              <React.Fragment>
                <option selected>Select</option>
              </React.Fragment>
            ) : null}
          </select>
        </div>
      </React.Fragment>
    );
  }
}

export default CampusLocationPreference;
