import React, { Component } from "react";
class DoYouHaveLaptop extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <div className="checkboxDiv">
            <p className="selectTitle">
              Do You Have Laptop {this.props.asterisk}
            </p>
            <input
              type="checkbox"
              onChange={this.props.handleChange}
              className="checkbox"
              name="haveLaptop"
            />
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default DoYouHaveLaptop;
