export const handleChange = e => dispatch => {
  dispatch({
    type: "HANDLE_CHANGE",
    payload: e.target
  });
};

export const handleChangeProvince = e => dispatch => {
  dispatch({
    type: "HANDLE_CHANGE_PROVINCE",
    payload: e.target.value
  });
};

export const handleChangeQualification = e => dispatch => {
  dispatch({
    type: "HANDLE_CHANGE_QUALIFICATION",
    payload: e.target.value
  });
};

export const handleChangeLearning = e => dispatch => {
  dispatch({
    type: "HANDLE_CHANGE_CHECKED",
    payload: e.target
  });
};

export const handleChangeLaptop = e => dispatch => {
  dispatch({
    type: "HANDLE_CHANGE_LAPTOP",
    payload: e.target
  });
};
