import React, { Component } from "react";
class HaveYouAnyProgrammingBackground extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <p className="selectTitle">
            Have You Any Programming Background? {this.props.asterisk}
          </p>
          <select
            name="programmingBackground"
            onChange={this.props.handleChange}
            className="selectInput form-control"
          >
            <option>Select</option>
            <option>Yes</option>
            <option>No</option>
          </select>
        </div>
      </React.Fragment>
    );
  }
}

export default HaveYouAnyProgrammingBackground;
