import React, { Component } from "react";
class Courses extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <p className="selectTitle">Course {this.props.asterisk}</p>
          <select
            onChange={this.props.handleChange}
            name="course"
            className="selectInput form-control"
          >
            {this.props.city === "Lahore" ? null : <option>Select</option>}
            {this.props.city === "Lahore" ? (
              <React.Fragment>
                <option>Select</option>
                <option>ArtificiaI Intelligence</option>
                <option>National Cyber Security</option>
                <option>Cloud Computing</option>
                <option>Internet of Things</option>
                <option>Software Engineering</option>
                <option>Augmented Reality</option>
                <option>Enterpreneurship</option>
              </React.Fragment>
            ) : null}
            {this.props.city === "Islamabad" ? (
              <React.Fragment>
                <option>ArtificiaI Intelligence</option>
                <option>National Cyber Security</option>
                <option>Software Engineering</option>
              </React.Fragment>
            ) : null}
            {this.props.city === "Peshawar" ? (
              <React.Fragment>
                <option>ArtificiaI Intelligence</option>
                <option>National Cyber Security</option>
                <option>Software Engineering</option>
              </React.Fragment>
            ) : null}
          </select>
        </div>
      </React.Fragment>
    );
  }
}

export default Courses;
