import React, { Component } from "react";
import InputTextField from "../../../common_components/textInput.jsx";
class QualificationCategories extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        {this.props.qualification === "O Levels" ||
        this.props.qualification === "A Levels" ? null : (
          <div className="selectInputDiv">
            <p className="selectTitle">
              Qualification Category {this.props.asterisk}
            </p>
            <select
              onChange={this.props.handleChange}
              name="qualificationCategories"
              className="selectInput form-control"
            >
              {this.props.qualification === "Matric" ||
              this.props.qualification === "Intermediate" ||
              this.props.qualification === "Undergraduate" ||
              this.props.qualification === "Graduate" ||
              this.props.qualification === "Post Graduate" ? (
                <option>Select</option>
              ) : (
                <option>Firstly Select Your Qualification</option>
              )}
              {this.props.qualification === "Matric" ? (
                <React.Fragment>
                  <option>Science</option>
                  <option>Arts</option>
                </React.Fragment>
              ) : null}
              {this.props.qualification === "Intermediate" ? (
                <React.Fragment>
                  <option>F.Sc (Pre-Medical)</option>
                  <option>F.Sc (Pre-Engineering)</option>
                  <option>ICS</option>
                  <option>I.Com</option>
                  <option>F.A</option>
                  <option>F.A General Science</option>
                </React.Fragment>
              ) : null}
              {this.props.qualification === "Undergraduate" ? (
                <React.Fragment>
                  <option>Computer Studies</option>
                  <option>Engineering</option>
                  <option>Accounting and Finance</option>
                  <option>Medical</option>
                  <option>Bussiness and Management</option>
                  <option>Humanities</option>
                  <option>Arts &amp; Design</option>
                  <option>Law</option>
                  <option>Media Studies</option>
                  <option>Others</option>
                </React.Fragment>
              ) : null}
              {this.props.qualification === "Graduate" ? (
                <React.Fragment>
                  <option>Others</option>
                </React.Fragment>
              ) : null}
              {this.props.qualification === "Post Graduate" ? (
                <React.Fragment>
                  <option>Others</option>
                </React.Fragment>
              ) : null}
            </select>
            {this.props.qualificationCategories === "Computer Studies" ||
            this.props.qualificationCategories === "Engineering" ||
            this.props.qualificationCategories === "Accounting and Finance" ||
            this.props.qualificationCategories === "Medical" ||
            this.props.qualificationCategories === "Bussiness and Management" ||
            this.props.qualificationCategories === "Humanities" ||
            this.props.qualificationCategories === "Arts & Design" ||
            this.props.qualificationCategories === "Law" ||
            this.props.qualificationCategories === "Media Studies" ||
            this.props.qualificationCategories === "Others" ? (
              <React.Fragment>
                <InputTextField
                  title="Degree Title"
                  inputClass="selectInput form-control"
                  subTitle=""
                  subTitleClass="none"
                  htmlFor="degree"
                  type="text"
                  name="degree"
                  handleChange={this.props.handleChange}
                  placeholder="Type your degree title"
                />
              </React.Fragment>
            ) : null}
          </div>
        )}
        {this.props.qualificationCategories === "Others" ? (
          <React.Fragment>
            <InputTextField
              title="Others"
              inputClass="selectInput form-control"
              subTitle=""
              subTitleClass="none"
              htmlFor="other"
              type="text"
              name="other"
              handleChange={this.props.handleChange}
              placeholder="Type your other qualification category"
            />
          </React.Fragment>
        ) : null}
        {this.props.qualification === "Undergraduate" ||
        this.props.qualification === "Graduate" ||
        this.props.qualification === "Post Graduate" ? (
          <React.Fragment>
            <div className="selectInputDiv">
              <label className="selectTitle">Major Subjects</label>
              <div className="row">
                <input
                  name="subjectOne"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control col-11 ml-3    col-md-2"
                />
                <input
                  name="subjectTwo"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control ml-3 col-11 ml-3 col-md-2"
                />
                <input
                  name="subjectThree"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control ml-3 col-11 ml-3 col-md-2"
                />
                <input
                  name="subjectFour"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control ml-3 col-11 ml-3 col-md-2"
                />
              </div>
              <div className="row">
                <input
                  name="subjectFive"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control col-11 ml-3 col-md-2"
                />
                <input
                  name="subjectSix"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control ml-3 col-11 ml-3 col-md-2"
                />
                <input
                  name="subjectSeven"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control ml-3 col-11 ml-3 col-md-2"
                />
                <input
                  name="subjectEight"
                  onChange={this.props.handleChange}
                  type="text"
                  id="CNIC or Bay Form #"
                  placeholder="CNIC or Bay Form #"
                  className="selectInput form-control ml-3 col-11 ml-3 col-md-2"
                />
              </div>
            </div>{" "}
          </React.Fragment>
        ) : null}
      </React.Fragment>
    );
  }
}

export default QualificationCategories;
