import React, { Component } from "react";
class StudentPhoto extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <p className="selectTitle">Photo {this.props.asterisk}</p>
          <div className="picture">
            <img
              src="http://www.westminsterbc.org.uk/wp-content/uploads/2015/09/men-placeholder.png"
              alt="img"
              className="img"
            />
            <div className="imgPolicy">
              <ol className="imgPolicyOl">
                <li>With white or blue background</li>
                <li>With white or blue background</li>
                <li>File type: jpg, jpeg, png</li>
                <li>Upload your recent passport size picture</li>
                <li>Your Face should be clearly visible, no side pose</li>
              </ol>
            </div>
          </div>
          <div className="custom-file mt-4">
            <input
              type="file"
              className="custom-file-input"
              id="customFileLang"
            />
            <label className="custom-file-label" htmlFor="customFileLang">
              Select Your Image
            </label>
          </div>
        </div>
        <div className="selectInputDiv">
          <p className="selectPara m-0">
            Your application will be rejected, if you do not follow the
            instructions for picture
          </p>
        </div>
      </React.Fragment>
    );
  }
}

export default StudentPhoto;
