import React, { Component } from "react";
import { Link } from "react-router-dom";
import CityOfStudy from "./ap_sp_components/cityOfStudy.jsx";
import Courses from "./ap_sp_components/courses.jsx";
import OnlineLearning from "./ap_sp_components/onlineLearning.jsx";
import InputTextField from "../../../common_components/textInput.jsx";
import ClassTimingPreference from "./ap_sp_components/timingPreference.jsx";
import CampusLocationPreference from "./ap_sp_components/locationPreference.jsx";
import HaveYouAnyProgrammingBackground from "./ap_sp_components/programmingBackground.jsx";
import HaveYouAnyPriorExperience from "./ap_sp_components/priorExperience.jsx";
import Gender from "../../../common_components/gender.jsx";
import StudentPhoto from "./ap_sp_components/studentPhoto.jsx";
import { handleChange, handleChangeLearning } from "../../../action/index";
import { connect } from "react-redux";
class Form extends Component {
  render() {
    console.log(this.props);
    return (
      <React.Fragment>
        <div className="form">
          <form action="#" className="formTag">
            <h1 className="formHeading">APPLICATION FORM</h1>
            <button className="lostBtn">Lost your admit card?</button>

            {/* Available Courses */}

            <h1 className="formHeading mt-3">Avialable Courses</h1>
            <CityOfStudy
              handleChange={e => this.props.handleChange(e)}
              asterisk="*"
            />
            <Courses
              asterisk="*"
              handleChange={e => this.props.handleChange(e)}
              city={this.props.city}
            />
            <OnlineLearning
              handleChangeLearning={e => this.props.handleChangeLearning(e)}
            />
            <ClassTimingPreference
              asterisk="*"
              handleChange={e => this.props.handleChange(e)}
            />
            <CampusLocationPreference
              asterisk="*"
              city={this.props.city}
              handleChange={e => this.props.handleChange(e)}
            />
            <HaveYouAnyProgrammingBackground
              asterisk="*"
              handleChange={e => this.props.handleChange(e)}
            />
            <HaveYouAnyPriorExperience
              asterisk="*"
              handleChange={e => this.props.handleChange(e)}
            />

            {/* Student Profile */}

            <h1 className="formHeading mt-3">Student Profile</h1>
            <InputTextField
              titleClass="selectTitle"
              title="Full Name"
              asterisk="*"
              subTitle="(Please specify your complete name as it appears on your CNIC.)"
              subTitleClass="nameSubTitle"
              htmlFor="fullName"
              required="required"
              type="text"
              inputClass="selectInput form-control"
              name="fullName"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Full Name"
            />
            <InputTextField
              titleClass="selectTitle"
              inputClass="selectInput form-control"
              asterisk="*"
              title="CNIC/B-Form # :"
              required="required"
              subTitle="( Enter your 13 digits CNIC / B-Form No. without hyphenation )"
              subTitleClass="nameSubTitle"
              htmlFor="CNIC or Bay Form #"
              type="text"
              errorTitle="Enter Your 13 Digit CNIC Number."
              name="Student_CNIC"
              handleChange={e => this.props.handleChange(e)}
              pattern="[0-9]{13}"
              placeholder="35xxxxxxxxxxx"
            />
            <InputTextField
              titleClass="selectTitle"
              inputClass="selectInput form-control"
              title="Email Address"
              asterisk="*"
              required="required"
              subTitle="(Provide valid email address, we will send verification email on this id)"
              subTitleClass="nameSubTitle"
              htmlFor="Email Address"
              errorTitle="Enter Your Valid Email Address."
              type="email"
              name="email"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your valid email address"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Your Mobile Number"
              subTitle=""
              subTitleClass="none"
              htmlFor="Mobile Number"
              type="tel"
              asterisk="*"
              required="required"
              pattern="[0-9]{11}"
              errorTitle="Enter Your 11 Digit Mobile Number."
              inputClass="selectInput form-control"
              name="Mobile_Number"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your valid Mobile Number (03XXXXXXXXX)"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Your Fb/LinkedIn Profile"
              subTitle="( Link )"
              subTitleClass="nameSubTitle"
              htmlFor="Your Fb/LinkedIn Profile"
              type="text"
              pattern="(www.|http://|https://)+[\w]/?"
              errorTitle="Enter Your Valid Url (http:// or https:// or www)."
              inputClass="selectInput form-control"
              name="fb_lindedIn_Profile"
              handleChange={e => this.props.handleChange(e)}
              placeholder="Your Fb/LinkedIn Profile Link"
            />
            <InputTextField
              titleClass="selectTitle"
              title="Date of Birth"
              subTitle=""
              subTitleClass="none"
              htmlFor="Date of Birth"
              type="date"
              asterisk="*"
              name="month"
              required="required"
              inputClass="selectInput form-control"
              handleChange={e => this.props.handleChange(e)}
            />
            <Gender
              asterisk="*"
              gender="studentGender"
              handleChange={e => this.props.handleChange(e)}
            />
            <StudentPhoto asterisk="*" />
            {this.props.city !== "" &&
            this.props.city !== "Select" &&
            this.props.course !== "" &&
            this.props.course !== "Select" &&
            this.props.locationPreference !== "" &&
            this.props.locationPreference !== "Select" &&
            this.props.programmingBackground !== "" &&
            this.props.programmingBackground !== "Select" &&
            this.props.timingPreference !== "" &&
            this.props.timingPreference !== "Select" &&
            this.props.priorExperience !== "" &&
            this.props.priorExperience !== "Select" &&
            this.props.fullName !== "" &&
            this.props.Student_CNIC !== "" &&
            this.props.email !== "" &&
            this.props.Mobile_Number !== "" &&
            this.props.month !== "" &&
            this.props.gender !== "" &&
            this.props.gender !== "Select" ? (
              <Link to="/remainingInfo" className="nextBtn">
                Next
              </Link>
            ) : (
              <Link to="" className="nextBtnDisable">
                Next
              </Link>
            )}
          </form>
        </div>
      </React.Fragment>
    );
  }
}

// state
const mapStateToProps = state => {
  return state;
};

// Actions
const mapDispatchToProps = { handleChange, handleChangeLearning };

export default connect(mapStateToProps, mapDispatchToProps)(Form);
