const initialState = {
  learning: "",
  city: "",
  programmingBackground: "",
  fb_lindedIn_Profile: "",
  priorExperience: "",
  fullName: "",
  Mobile_Number: "",
  email: "",
  Student_CNIC: "",
  course: "",
  month: "",
  timingPreference: "",
  locationPreference: "",
  student_permanant_address: "",
  student_present_address: "",
  district: "",
  province: "",
  qualification: "",
  qualificationCategories: "",
  other: "",
  subjectOne: "",
  institute: "",
  studentGender: "",
  fatherOrGuardianGender: "",
  subjectTwo: "",
  subjectThree: "",
  subjectFour: "",
  degree: "",
  haveLaptop: "",
  subjectFive: "",
  subjectSix: "",
  subjectSeven: "",
  subjectEight: "",
  father_guardian_name: "",
  father_guardian_CNIC: "",
  father_guardian_email: "",
  father_guardian_number: "",
  father_guardian_permanant_address: "",
  father_guardian_present_address: ""
};

export const reducer = (state = initialState, action) => {
  if (action.type === "HANDLE_CHANGE") {
    return {
      ...state,
      [action.payload.name]: action.payload.value
    };
  } else if (action.type === "HANDLE_CHANGE_PROVINCE") {
    return {
      ...state,
      province: action.payload
    };
  } else if (action.type === "HANDLE_CHANGE_QUALIFICATION") {
    return {
      ...state,
      qualification: action.payload,
      qualificationCategories: ""
    };
  } else if (action.type === "HANDLE_CHANGE_CHECKED") {
    return {
      ...state,
      learning: action.payload.checked
    };
  } else if (action.type === "HANDLE_CHANGE_LAPTOP") {
    return {
      ...state,
      haveLaptop: action.payload.checked
    };
  } else {
    return state;
  }
};
