import React, { Component } from "react";
class OnlineLearning extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="selectInputDiv">
          <div className="checkboxDiv">
            <p className="selectTitle">Online Learning</p>
            <input
              type="checkbox"
              name="learning"
              onChange={this.props.handleChangeLearning}
              className="checkbox"
              id=""
            />
          </div>
          <p className="checkboxPara">
            Only check this box if you are interested in joining the class
            online. For distance learning, you must be in the city which you
            have selected in 'City of Study' option above to attempt all exams.
          </p>
        </div>
      </React.Fragment>
    );
  }
}

export default OnlineLearning;
